.App {
  position: absolute;
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(110deg, #818080 60%, rgb(162, 160, 160) 60%);
}
.ContTxt{
  position: relative;
  margin: 20px;
  width: 40%;
  height: 50px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.txt{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: larger;
  border: none;
  background: ghostwhite;
}
.icobt{
  position: relative;
  width: 20px;
  height: 20px;
}
.combac{
  position: absolute;
  top: 1px;
  left: 1px;
  width: 60px;
  height: 50px;
  z-index: 100;
  border: none;
  text-align: center;
  font-size: medium;
  background: ghostwhite;
  transition: 0.3s;
}
.combac:hover{
  cursor: pointer;
}
.hamb{
  position: relative;
  top: -51px;
  margin-left: auto;
  margin-right: -2px;
  width: 50px;
  height: 50px;
  background: transparent;
  border: none;
  border-radius: 0.2em;
  transition: 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hamb:hover{
  cursor: pointer;
  background-color: #e5e5e5;
}
.hamb:hover:disabled{
  cursor: default;
  background: transparent;
}
.button{
  position: relative;
  margin-top: 30px;
width: 20%;
  height: 50px;
  font-size: large;
  border: none;
  transition: 0.2s;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background: ghostwhite;
  display: flex;
  justify-content: center;
  align-items: center;

}
.button:hover{
  cursor: pointer;
  color: #fafff8;
  background: rgba(100, 98, 100, 0.3);
}
.button:disabled{
  cursor: default;
  background: ghostwhite;
  color: rgba(129, 128, 128, 0.72);
}
.divlist{
  position: absolute;
  z-index: 101;
  background-color: #c7c5c4;
  border-radius: 0.2em;
  transition: 0.3s;
  display: flex;
  justify-content: start;
  flex-direction: column;

}
.listOn{ left: 25%;
  top: 300px;
  width: 50%;
  height: auto;}
.listOff{ left: 25%;
  top: 100%;
  width: 50%;
  height: 0px;}

.divSec{
  position: relative;
  width: 100%;
}
.secexit{
  display: flex;
  align-items: center;
  height: 30px;
  padding: 5px;
}
.secexit button{
  width: 30px;
  height: 30px;
  background-color: transparent;
  border:none;
  margin-left: auto;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.3em;
  transition: 0.2s;
}
.secexit button:hover{
  cursor: pointer;
  background-color: darkgrey;
}
.tablesec{
  position: relative;
  width: 96%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;

}
.seeNothing{
  display: none;
}
.opa{
  opacity: 0.6;
}